.text-yellow-gradient {
  background: -webkit-linear-gradient(180deg, #ebc878 0%, #c69649 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.yellow-btn {
  position: relative;
  padding: 16px 32px;
  border-radius: 5px;
  z-index: 5;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.yellow-btn > :not(:last-child) {
  margin-right: 10px;
}

.yellow-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, #ebc878 0%, #c69649 100%);
  z-index: -1;
}

